@font-face {
  font-family: "Arial";
  src: url("../fonts/arial/ArialMT.woff2") format("woff2"), url("../fonts/arial/ArialMT.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Arial";
  src: url("../fonts/arial/Arial-ItalicMT.woff2") format("woff2"), url("../fonts/arial/Arial-ItalicMT.woff") format("woff");
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}
*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  font-size: 11px;
  font-weight: normal;
  font-style: normal;
  font-style: normal;
  color: #666;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  flex: 1;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.container {
  width: 96%;
  margin: auto;
}

img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.lock {
  position: fixed;
}

.header {
  padding: 1em 0;
  background-color: #fff;
}
.header__wrapper {
  display: flex;
  justify-content: space-evenly;
  min-height: 81px;
}
.header__logo {
  display: block;
  width: 340px;
  height: 81px;
}
.header__nav {
  display: flex;
  justify-content: center;
  position: sticky;
  top: 0;
  background-color: #fff;
}
.header__menu-btn {
  display: none;
  z-index: 102;
}

.search {
  display: flex;
  align-items: flex-end;
}
.search__wrapper {
  padding: 15px 0;
}
.search__input {
  width: 213px;
  border: 1px solid #999;
  outline: none;
  padding: 3px;
}
.search__input:focus {
  border-color: #000;
}
.search__btn {
  padding: 5px;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
  color: inherit;
  text-decoration: none;
}
.search__btn:hover {
  color: #000;
}

.nav {
  padding: 11px 0;
}
.nav__list {
  display: flex;
  list-style: none;
  border-bottom: 1px solid #666;
  justify-content: center;
  padding-bottom: 1em;
}
.nav__item {
  position: relative;
  padding: 0 7px;
}
.nav__item:not(:last-child):after {
  content: "|";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}
.nav__link {
  color: inherit;
  text-decoration: none;
}
.nav__link:hover {
  color: #000;
}

.menu-btn {
  position: relative;
  border: none;
  width: 40px;
  height: 40px;
  background-color: transparent;
  cursor: pointer;
}
.menu-btn__line {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  display: block;
  width: 30px;
  height: 2px;
  background: #999;
  opacity: 1;
  transition: 1s;
}
.menu-btn__line--top {
  width: 20px;
  height: 2px;
  background: #999;
  position: absolute;
  top: 0;
  left: 0;
  transition: 1s;
}
.menu-btn__line--bottom {
  width: 40px;
  height: 2px;
  background: #999;
  position: absolute;
  top: auto;
  left: 0;
  bottom: 0;
  transition: 1s;
}

.menu {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: #666;
  color: #fff;
  z-index: -100;
  opacity: 0;
  transition: 1s;
}
.menu__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 96%;
  margin: auto;
  padding: 3em;
}
.menu__search {
  margin-bottom: 2em;
}
.menu__list {
  display: flex;
  list-style: none;
  flex-direction: column;
}
.menu__item {
  margin-bottom: 2em;
}
.menu__link {
  padding: 7px;
}
.menu__btn-clouse {
  align-self: end;
  position: relative;
  border: none;
  width: 40px;
  height: 40px;
  background-color: transparent;
  cursor: pointer;
  margin-bottom: 1.5em;
}
.menu__btn-clouse--top {
  position: absolute;
  top: 45%;
  left: 0;
  width: 40px;
  height: 2px;
  background: #999999;
  opacity: 1;
  transform: rotate(45deg);
}
.menu__btn-clouse--bottom {
  position: absolute;
  top: 45%;
  right: 0;
  width: 40px;
  height: 2px;
  background: #999999;
  opacity: 1;
  transform: rotate(-45deg);
}

.menu-vizible {
  z-index: 100;
  opacity: 1;
  transition: 1s;
}

.breadcrumb {
  padding: 0 0 11px 0;
}
.breadcrumb__list {
  display: flex;
  list-style: none;
}
.breadcrumb__item {
  position: relative;
  padding: 0 7px;
}
.breadcrumb__item:not(:last-child):after {
  content: "|";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}
.breadcrumb__link {
  color: inherit;
  text-decoration: none;
}
.breadcrumb__link:hover {
  text-decoration: underline;
  color: #999999;
}

.books-list {
  padding: 1em 0;
}
.books-list__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  list-style: none;
}
.books-list__item {
  min-width: 210px;
  max-width: 217px;
  min-height: 260px;
  margin-bottom: 1em;
  padding: 0 7px;
}
.books-list__link {
  display: block;
  width: 216px;
  height: 216px;
  margin-bottom: 1em;
}
.books-list__title {
  font-size: 12px;
  font-style: italic;
  text-align: center;
}

.search__title {
  font-size: 24px;
  font-weight: bold;
  color: #000;
  margin-bottom: 1em;
}
.search__text {
  font-size: 16px;
}

.footer {
  padding: 1em 0 2em 0;
}
.footer_wrapper {
  display: flex;
  justify-content: center;
  border-top: 1px solid #666;
}
.footer__copyright {
  padding-top: 1em;
  font-size: 0.9em;
}
.footer__link {
  color: inherit;
  text-decoration: none;
}
.footer__link:hover {
  text-decoration: underline;
}

@media screen and (max-width: 767px) {
  .header {
    position: sticky;
    top: 0;
  }
  .header__wrapper {
    align-items: center;
  }
  .header__nav {
    display: none;
  }
  .header__search {
    display: none;
  }
  .header__menu-btn {
    display: block;
  }
  .header__menu-btn--active .menu-btn__line {
    opacity: 0;
    transition: 1s;
  }
  .header__menu-btn--active .menu-btn__line--top {
    top: 45%;
    left: 0;
    width: 40px;
    opacity: 0;
    transform: rotate(45deg);
    transition: 1s;
  }
  .header__menu-btn--active .menu-btn__line--bottom {
    width: 40px;
    top: 45%;
    left: 0;
    opacity: 0;
    transform: rotate(-45deg);
    transition: 1s;
  }
}
@media screen and (max-width: 480px) {
  .header__wrapper {
    justify-content: space-between;
  }
  .header__logo {
    width: 245px;
    height: 61px;
  }
}