@media screen and ( max-width: 767px )
  .header
    position: sticky
    top: 0
    &__wrapper
      // justify-content: space-between
      align-items: center
    &__nav
      display: none
    &__search
      display: none
    &__menu-btn
      display: block
      &--active
        .menu-btn__line
          opacity: 0
          transition: 1s
          &--top
            top: 45%
            left: 0
            width: 40px
            // transform: translateY(-50%)
            // background: #000
            opacity: 0
            transform: rotate(45deg)
            transition: 1s
          &--bottom
            width: 40px
            top: 45%
            left: 0
            // transform: translateY(-45%)
            opacity: 0
            transform: rotate(-45deg)
            transition: 1s

@media screen and ( max-width: 480px )
  .header
    &__wrapper
      justify-content: space-between
    &__logo
      width: 245px
      height: 61px
